











import {Toast} from "mint-ui";
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: "index-credit",
components: {
XHeader
},
data(){
return {
title: "信用卡",
back: true,
morebankcard:false,
morebankcardnext:false,
bgfff: "bgfff",
content: [],
contentnext:[],
twoRows:true,
cardrows:true,
}
},

methods:{
handleScroll(ev){
ev=ev||window.event;
var top = $(ev.target).scrollTop();
var top2 = ev.target.scrollTop;

if(top>70){
this.bgfff = '';
}else{
this.bgfff = 'bgfff';
}
},
showmore(all){
if(all===true){
this.twoRows=false;
return false;
}
this.twoRows=!this.twoRows;
},

showmoresec(all){
if(all===true){
this.cardrows=false;
return false;
}
this.cardrows=!this.cardrows;
},
getVisa(){
this.$axios.post("/api/visa/get_visa")
.then((results) => {
if(results.code==200){
this.content=results.data.bank_list;
let newprice=results.data.hot_visa;

for(var i in newprice){
if(!newprice[i].hot_desc){
newprice[i].hot_desc=0;
}
};
newprice.sort(this.sortprice);


this.contentnext=newprice;

}else{
Toast(res.msg)
};
});
},
sortprice(a,b){
return a.hot_desc-b.hot_desc;
},
getBank(){

this.$axios.post("/api/visa/get_bank_visa")
.then((results) => {
if(results.code==200){

console.log(results.data)
}else{
};
});
},
},
computed: {



},
created () {
this.getVisa();
},
mounted(){





},
beforeDestroy () {


},
filters:{
getnum(value){
return parseFloat(value/1000).toFixed(1);
},
}
};



